import { KeyCode, KeyUtils } from "@devexpress/utils/lib/utils/key";
import { CalendarKbdStrategy } from "./calendar-kbd-strategy";
import { CalendarNavigationAreaType } from "../calendar-types";
import { CssClasses } from "../../../css-classes";
export class CalendarFooterKbdStrategy extends CalendarKbdStrategy {
    constructor(calendar, targetElement) {
        super(calendar, targetElement);
        this.itemSelector = `:scope > .${CssClasses.Button}`;
    }
    getShortcutContext() {
        return {
            AreaType: CalendarNavigationAreaType.Footer,
            ButtonType: this.selectedItemIndex
        };
    }
    queryItems() {
        return this.queryItemsBySelector(this.itemSelector);
    }
    activate() {
        this.selectedItemIndex = this.isFocusedFromOutside() ? this.itemCount - 1 : 0;
        super.activate();
    }
    handleKeyDown(evt) {
        switch (KeyUtils.getEventKeyCode(evt)) {
            case KeyCode.Tab:
                return evt.shiftKey ? this.moveToPrevItem() : this.moveToNextItem();
            case KeyCode.Space:
            case KeyCode.Enter:
                this.performShortcutEvent(evt);
                return true;
            case KeyCode.Left:
            case KeyCode.Up:
            case KeyCode.Home:
            case KeyCode.PageUp:
            case KeyCode.Right:
            case KeyCode.Down:
            case KeyCode.End:
            case KeyCode.PageDown:
                return true;
        }
        return super.handleKeyDown(evt);
    }
    moveToPrevItem() {
        if (this.selectedItemIndex > 0) {
            this.selectItem(this.selectedItemIndex - 1);
            return true;
        }
        return false;
    }
    moveToNextItem() {
        if (this.selectedItemIndex < this.itemCount - 1) {
            this.selectItem(this.selectedItemIndex + 1);
            return true;
        }
        return false;
    }
}
